@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,900&display=swap);
body {
  margin: 0 auto;
  max-width: 1440px;
}

.header {
    /* position: fixed; */
  display: -webkit-flex;
  display: flex;
  height: 64px;
  width: 100%;
  background-color: #2196f3;
  vertical-align: middle;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.24);
  margin-bottom: 52px;
}

.header__link {
  display: inline-block;
  border: none;
  color: white;
  background: none;
  cursor: pointer;
  outline: none;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 400;
  background-color: #1e88e5;
  text-decoration: none;
  text-align: center;
  padding-top: 23px;
}

.header__link:first-child {
  width: 120px;
  margin-left: 4.54%;
}

.header__link:last-child {
  width: 173px;
}

.header__link:hover {
  background-color: #1e88e5;
}

.header__opacity {
  opacity: 0.7;
  background-color: #2196f3;
}

.cats {
  width: 100%;
  padding: 0 3.74%;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin: 0 auto;
}

.cat-block {
  position: relative;
  width: 225px;
  height: 225px;
  margin: 0 auto 48px auto;
}

.cat-block:hover .img {
  width: 107%;
  height: 107%;
  box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24), 0px 9px 18px rgba(0, 0, 0, 0.18);
}



.cats__heart {
  display: none;
  position: absolute;
  bottom: 0;
  left: 80%;
  cursor: pointer;
}

.cat-block:hover .cats__heart {
  display: block;
}

.cats__favorite {
  position: absolute;
  cursor: pointer;
  bottom: 5%;
  left: 70%;
}

 .cat-block:hover .cats__favorite {
  bottom: 0;
  left: 80%;
}

.cats__load-more-cats-link {
    margin: 0 auto;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight:400;
}

.cats__load-more-cats-link p {
    margin: 0 0 31px 0;
    cursor: pointer;
    text-align: center;
}
.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
.favorites {
    -webkit-justify-content:center;
            justify-content:center;
    margin: 0 auto;
    -webkit-align-items:center;
            align-items:center;
}
